            <p className="Privacy"><strong>The purpose for which we may process information about you are as follows:</strong> Provide You Services: We process your account and profile data, device information, usage data, location information, and Smart Device related information to provide you with our Products and Services that you have requested or purchased. The legal basis for this processing is to perform our contract with you according to our Terms of Use., Improve Our Services: We process your device information, usage data, location information and Smart Device related information to ensure the functions and safety of our Products, to develop and improve our Products and Services, to analyze the efficiency of our operations, and to prevent and trace fraudulent or inappropriate usage. The legal basis for this processing is to perform our contract with you according to our Terms of Use. Non-marketing Communication: We process your Personal Data to send you important information regarding the Services, changes to our terms, conditions, and policies and/or other administrative information. At the same time, we will also send you notifications related to the services you have purchased, such as alert services. You can check the [App Notification] in the App to manage these communications. When you decide not to enable the Notifications function, we will no longer process your information for such purpose. The legal basis for this processing is to perform our contract with you according to our Terms of Use. Marketing Communication: We may process your Personal Data to provide marketing and promotional materials to you on the Services. If we do so, each communication we send you will contain instructions permitting you to opt-out of receiving future communications of that nature. If you do not agree to Linkbase sending you marketing communications, you may opt out when you enter the App, or by changing your preferences in [Privacy Settings] of the App. The legal basis for this processing is your consent.. Personalization: We may process your account and profile data, usage data, device information to personalize product design and to provide you with services tailored for you, such as recommending and displaying information and advertisements regarding products suited to you, and to invite you to participate in surveys relating to your use of the Services. If you do not allow us to process your Personal Data for personalization, you may opt out when you enter the App, or by changing your preferences in [Privacy Settings] in the App. The legal basis for this processing is your consent. Legal Compliance: We may process your Personal Data as we believe to be necessary or appropriate: (a) to comply with applicable laws and regulations; (b) to comply with legal process; (c) to respond to requests from public and government authorities (d) to enforce our terms and conditions; (e) to protect our operations, business and systems; (f) to protect our rights, privacy, safety or property, and/or that of other users, including you; and (g) to allow us to pursue available remedies or limit the damages that we may sustain. If there is any change in the purposes for processing your personal data, we will inform you via email and/or a prominent notice on our website of such changes of purposes, and choices you may have regarding your Personal Data.</p>
